import axios from 'axios';

//get games from nba api
export function getGames(date){
    return new Promise((resolve, reject) => {
        axios({
            method: 'post',
            url: '/getgames',
            params: {gameDate:date}, 
            headers: {
            'Content-Type': 'application/json'
            }, 
          })
        .then(response => {
          resolve(response.data);
        })
        .catch(error => reject(error));

    });

}

//get boxscore from nba api
export function getBoxScore(date, id){
  return new Promise((resolve, reject) => {
      axios({
          method: 'post',
          url: '/getboxscore',
          params: {gameDate:date, gameId:id}, 
          headers: {
          'Content-Type': 'application/json'
          }, 
        })
      .then(response => {
        resolve(response.data.game);
      })
      .catch(error => reject(error));
  });
}

//get play by play list from nba api
export function getPlayByPlay(date, id, period){
  return new Promise((resolve, reject) => {
      axios({
          method: 'post',
          url: '/getplaybyplay',
          params: {gameDate:date, gameId:id, gamePeriod:period}, 
          headers: {
          'Content-Type': 'application/json'
          }, 
        })
      .then(response => {
        //console.log(response.data);
        resolve(response.data.game);
      })
      .catch(error => reject(error));

  });
}
