//initial state of redux store
const initialState = {
    notif: {
        score:0,
        quarter:1,
        teamFould:0
    },
    isNotif:{
        score:false,
        quarter:false,
        teamFoul:false
    },
    date:'',
    datePicker:new Date(),
    dateGames:[],
    gameHistory:[],
    boxScore:0,
    gameBoxScore: null,
    showPlayByPlay: false,
    playByPlay: null
}
//reducer function when redux action dispatched
const reducer = (
    state = initialState, action) => {
    switch(action.type){
        case 'SET_NOTIF':{
            return Object.assign({}, state, {notif: action.payload});
        }
        case 'SET_IS_NOTIF': {
            return Object.assign({}, state, {isNotif: action.payload});
        }
        case 'SET_DATE': {
            return Object.assign({}, state, {date: action.payload});
        }
        case 'SET_DATE_PICKER': {
            return Object.assign({}, state, {datePicker: action.payload});
        }
        case 'SET_DATE_GAMES': {
            return Object.assign({}, state, {dateGames: action.payload});
        }
        case 'SET_BOX_SCORE': {
            return Object.assign({}, state, {boxScore: action.payload});
        }        
        case 'SET_GAME_BOX_SCORE': {
            return Object.assign({}, state, {gameBoxScore: action.payload});
        }   
        case 'SET_GAME_HISTORY': {
            let currentGH = state.gameHistory.slice();
            return Object.assign({}, state, {gameHistory: [...currentGH, ...action.payload]});
        }
        case 'SET_SHOW_PLAY_BY_PLAY': {
            return Object.assign({}, state, {showPlayByPlay: action.payload});
        }
        case 'SET_PLAY_BY_PLAY': {
            return Object.assign({}, state, {playByPlay: action.payload});
        }
        default: return state;
    }
};

export default reducer;