import React, {Component} from 'react';
import Constants from '../scripts/constants';
import LoadingComponent from './loadingComponent';
import moment from 'moment';
import {getPlayByPlay} from '.././scripts/nbaApi';
import Utility from '../scripts/utility';
import '../scss/boxscore.scss';


class BoxScoreComponent extends Component{
    isPlayByPlay = false;
    pbpPeriod = 1;
    constructor(props){
        super(props);
        this.showPlayByPlay = this.showPlayByPlay.bind(this);
        this.pushMessage = this.pushMessage.bind(this);
    }
    
    //show list of plays in a specific period
    showPlayByPlay(game, period){
        this.pbpPeriod = period;
        this.props.setPlayByPlay(null);

        //period 0 hide play by play list, otherwise show
        if(period===0){
            this.props.setShowPlayByPlay(false);
        }
        else{
            //get play by play list and show it
            this.props.setShowPlayByPlay(true);
            getPlayByPlay(game.startDateEastern, game.gameId, period).then(
                (response) => { 
                    if(response != null){
                        this.props.setPlayByPlay(response.play);
                    }
                    else{
                        this.props.setPlayByPlay(null);
                    }
                },
                (error) => {
                  console.log(error);
              }
            );  
        }

    }

    pushMessage(title, message){
        // Trigger custom push event from client.js
        document.dispatchEvent(new CustomEvent("pushbaby", {detail:{title: title, message: message}}));
    }

    render(){
        let game = this.props.gameBoxScore;
        let boxscore = this.props.boxScore;
        return(
            <div className="boxscore-container" key={game.gameId}>
            <div className="boxscore-arena">
                {game.arena.name + " " + game.arena.city + ", " + game.arena.country}
            </div>
            <div className="boxscore-schedule">
                {moment(game.startDateEastern).format('dddd, MMMM Do YYYY')}
                {
                game.isGameActivated?
                    <div className="boxscore-live">Live Now</div>
                :''
                }    
            </div>
            <div className="boxscore-team-container">
                <div className="boxscore-team">
                    <div className="team-title" style={{color:Constants.teamColor[game.hTeam.triCode]}}>{Constants.teams[game.hTeam.triCode]}</div>
                    <div className="team-standing">({game.hTeam.win + '-' + game.hTeam.loss})</div>
                    <div className="team-logo">
                        <img title={Constants.teams[game.hTeam.triCode]} src={'https://www.nba.com/.element/img/1.0/teamsites/logos/teamlogos_500x500/' + game.hTeam.triCode.toLowerCase() + '.png'}/>
                    </div>
                    <div className={((parseInt(game.hTeam.score) > parseInt(game.vTeam.score))?'winner ':'') + 'team-score'}>
                        {game.hTeam.score}
                    </div>
                </div>
                <div className="boxscore-team">
                    <div className="team-title" style={{color:Constants.teamColor[game.vTeam.triCode]}}>{Constants.teams[game.vTeam.triCode]}</div>
                    <div className="team-standing">({game.vTeam.win + '-' + game.vTeam.loss})</div>
                    <div className="team-logo">
                        <img title={Constants.teams[game.vTeam.triCode]} src={'https://www.nba.com/.element/img/1.0/teamsites/logos/teamlogos_500x500/' + game.vTeam.triCode.toLowerCase() + '.png'}/>
                    </div>
                    <div className={((parseInt(game.vTeam.score) > parseInt(game.hTeam.score))?'winner ':'') + 'team-score'}>
                        {game.vTeam.score}
                    </div>
                </div>
                {
                    game.hasGameBookPdf?''
                    :
                        game.period.current == 0?
                            <div className="boxscore-not-started">Not Started</div>
                        :
                            game.period.current <= 4 && game.period.isEndOfPeriod?
                                <div className="boxscore-live">Live Now</div>
                            :''
                }
                {
                    boxscore.visitor.score !== ""?
                        <div className="boxscore-period">
                            <div className="period-row">
                                <div className="period-team head">Team</div>
                                <div className="period-quarter">Q1</div>
                                <div className="period-quarter">Q2</div>
                                <div className="period-quarter">Q3</div>
                                <div className="period-quarter">Q4</div>
                            </div>
                            {
                                [boxscore.home, boxscore.visitor].map((team, index) => {
                                    return <div className="period-row">
                                                <div style={{color:Constants.teamColor[team.abbreviation]}} className="period-team" key={'x'+index}>{team.nickname}</div>
                                                {
                                                    team.linescores.period.map((period, index2) => {
                                                    return index2<5?<div key={'y'+index+index2}>{period.score}</div>:''
                                                    })
                                                }
                                        </div>
                                })
                            }
                            <div className="playbyplay-container">
                                <div className="playbyplay-button">
                                    <button onClick={this.showPlayByPlay.bind(this, game, (this.props.showPlayByPlay?0:1))}>
                                        {(this.props.showPlayByPlay?'Hide':'Show') + ' '}
                                        Play by Play
                                    </button>
                                    <button className="close" onClick={this.props.setBoxScore.bind(this, 0)}>
                                        Close Boxscore
                                    </button>
                                </div>
                                {
                                    this.props.showPlayByPlay?
                                    <div>
                                    <div className="period-instruct">
                                        Click an item below to push the data to your machine.
                                    </div>
                                    <div className="period-container">
                                        <div className={'period ' + (this.pbpPeriod===1?'pbp':'')} onClick={this.showPlayByPlay.bind(this, game, 1)}>Q1</div>
                                        <div className={'period ' + (this.pbpPeriod===2?'pbp':'')} onClick={this.showPlayByPlay.bind(this, game, 2)}>Q2</div>
                                        <div className={'period ' + (this.pbpPeriod===3?'pbp':'')} onClick={this.showPlayByPlay.bind(this, game, 3)}>Q3</div>
                                        <div className={'period ' + (this.pbpPeriod===4?'pbp':'')} onClick={this.showPlayByPlay.bind(this, game, 4)}>Q4</div>
                                    </div>
                                    {
                                        this.props.playByPlay ===null?
                                            <div className="loading-container">
                                                <LoadingComponent type={'spin'} color={'#8d8f9b'}/>
                                            </div>
                                        :''
                                    }
                                    {
                                        this.props.playByPlay !== null?
                                            <div className="playbyplay-list" >
                                                {
                                                    this.props.playByPlay.reverse().map((play, index)=>{
                                                        return <div className="play" key={'pbp'+index}
                                                                onClick={
                                                                    this.pushMessage.bind(this, ('('+(play.clock === ""?'12:00':play.clock) + ') ' + game.vTeam.triCode + ' ' + Utility.addZero(play.visitor_score) + '-' +
                                                                    Utility.addZero(play.home_score) + ' ' + game.hTeam.triCode), play.description)
                                                                }>
                                                            <div className="play-time">
                                                                <div className="time-time">
                                                                    {
                                                                        play.clock === ""?
                                                                            '12:00'
                                                                        :
                                                                            play.clock
                                                                    }
                                                                </div>
                                                                <div className="play-description">
                                                                    {play.description}
                                                                </div>                                                                
                                                                <div className="time-score">
                                                                    {
                                                                        <span>
                                                                            <span style={{color:Constants.teamColor[game.vTeam.triCode]}}>{game.vTeam.triCode + ' '}</span> 
                                                                            <span style={{fontWeight:'bold'}}>{Utility.addZero(play.visitor_score) + ' - ' + Utility.addZero(play.home_score)}</span>
                                                                            <span style={{color:Constants.teamColor[game.hTeam.triCode]}}>{ ' ' + game.hTeam.triCode}</span> 
                                                                        </span>
                                                                    }
                                                                </div>
                                                                <div className="play-logo">
                                                                    <img src={'https://www.nba.com/.element/img/1.0/teamsites/logos/teamlogos_500x500/' + (play.visitor_score>play.home_score?game.vTeam.triCode:game.hTeam.triCode).toLowerCase() + '.png'}/>
                                                                </div>
                                                            </div>
                                                        </div>                                    
                                                    })
                                                }
                                            </div>
                                        :''
                                    }
                                </div>
                                    :''
                                }
                            </div>
                        </div>
                    :''
                }

            </div>

        </div>
        );
    }
}

export default BoxScoreComponent;