import React, { Component } from 'react';
import Constants from '../scripts/constants';
import '../scss/gamelist.scss';
import moment from 'moment';
 
class GameListComponent extends Component{
    render() {
        return (
            <div className="gamelist-container">
                <div className="gamelist-header" style={{display: this.props.title===null?'none':''}}>
                    &nbsp;
                    <h1>{this.props.title}</h1>
                </div>
                <div className="gamelist-games">
                    {
                        this.props.gameList.map((game, index) => {
                            return <div onClick={this.props.showBoxScore.bind(this, game)}
                            className="game-box" key={game.gameId}>
                                        <div className="game-schedule">
                                            {moment(game.startDateEastern).format('dddd, MMMM Do')}
                                        </div>
                                        <div className="game-team-container">
                                            <div className="game-team">
                                                <div className="team-title" style={{color:Constants.teamColor[game.hTeam.triCode]}}>{Constants.teams[game.hTeam.triCode]}</div>
                                                <div className="team-standing">({game.hTeam.win + '-' + game.hTeam.loss})</div>
                                                <div className="team-logo">
                                                    <img title={Constants.teams[game.hTeam.triCode]} src={'https://www.nba.com/.element/img/1.0/teamsites/logos/teamlogos_500x500/' + game.hTeam.triCode.toLowerCase() + '.png'}/>
                                                </div>
                                                <div className={((parseInt(game.hTeam.score) > parseInt(game.vTeam.score))?'winner ':'') + 'team-score'}>
                                                    {game.hTeam.score}
                                                </div>
                                            </div>
                                            <div className="game-team">
                                                <div className="team-title" style={{color:Constants.teamColor[game.vTeam.triCode]}}>{Constants.teams[game.vTeam.triCode]}</div>
                                                <div className="team-standing">({game.vTeam.win + '-' + game.vTeam.loss})</div>
                                                <div className="team-logo">
                                                    <img title={Constants.teams[game.vTeam.triCode]} src={'https://www.nba.com/.element/img/1.0/teamsites/logos/teamlogos_500x500/' + game.vTeam.triCode.toLowerCase() + '.png'}/>
                                                </div>
                                                <div className={((parseInt(game.vTeam.score) > parseInt(game.hTeam.score))?'winner ':'') + 'team-score'}>
                                                    {game.vTeam.score}
                                                </div>
                                            </div>
                                            {
                                                game.hasGameBookPdf?''
                                                :
                                                    game.period.current == 0?
                                                        <div className="game-not-started">Not Started</div>
                                                    :''
                                            }                                     
                                            
                                        </div>
                                        {
                                            game.isGameActivated?
                                                <div className="game-live">Live Now</div>
                                            :''
                                        }                                              
                                        <div className="game-detail">
                                            <div className="detail-season">
                                                {
                                                    game.playoffs != null?
                                                        game.playoffs.seriesSummaryText
                                                    :
                                                        'Season ' + game.seasonYear  + ' ' + Constants.season[game.seasonStageId-1]
                                                }
                                            </div>
                                            <div className="detail-arena">
                                                {game.arena.name + " " + game.arena.city + ", " + game.arena.country}
                                            </div>
                                        </div>
                                    </div>
                        })
                    }

                </div>
            </div>
        );
    }
};

export default GameListComponent;