import {connect} from 'react-redux';
import HomeComponent from '../components/homeComponent';
import {
    setIsNotif, 
    setNotif, 
    setDate, 
    setDateGames, 
    setGameHistory, 
    setDatePicker, 
    setBoxScore, 
    setGameBoxScore,
    setShowPlayByPlay,
    setPlayByPlay
} from '../actions/pushAction';

const mapStateToProps = (state) => {
    return {
        notif: state.notif,
        isNotif: state.isNotif,
        date: state.date,
        dateGames: state.dateGames,
        gameHistory: state.gameHistory,
        datePicker: state.datePicker,
        boxScore: state.boxScore,
        gameBoxScore: state.gameBoxScore,
        showPlayByPlay: state.showPlayByPlay,
        playByPlay: state.playByPlay
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        setNotif: (notif) => dispatch(setNotif(notif)),
        setIsNotif: (isNotif) => dispatch(setIsNotif(isNotif)),
        setDate: (date) => dispatch(setDate(date)),
        setDateGames: (games) => dispatch(setDateGames(games)),
        setBoxScore: (boxScore) => dispatch(setBoxScore(boxScore)),
        setGameHistory: (games) => dispatch(setGameHistory(games)),
        setDatePicker: (date) => dispatch(setDatePicker(date)),
        setGameBoxScore: (gameBoxScore) => dispatch(setGameBoxScore(gameBoxScore)),
        setShowPlayByPlay: (showPlayByPlay) => dispatch(setShowPlayByPlay(showPlayByPlay)),
        setPlayByPlay: (playByPlay) => dispatch(setPlayByPlay(playByPlay)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeComponent); 