import React, { Component } from 'react';
import {getGames, getBoxScore, push} from '.././scripts/nbaApi';
import DateComponent from './dateComponent';
import LoadingComponent from './loadingComponent';
import GameListComponent from './gameListComponent';
import BoxScoreComponent from './boxscoreComponent';
import moment from 'moment';
import images from './../scripts/imgimporter';
import '../scss/home.scss';

class HomeComponent extends Component{
    pastDays = 3;
    constructor(props){
        super(props);
        this.updateDate = this.updateDate.bind(this);
        this.showBoxScore = this.showBoxScore.bind(this);
    }

    componentDidMount(){
        const interval = 'days';
        let tempGameHistory = [];
        let datesHistory = [];
        let historyCounter = 0;
        let numDays = this.pastDays;
        for(let x=1; x <= numDays; x++){
            datesHistory.push(moment("20190421").subtract(x, interval).format("YYYYMMDD"));
        }
        const tempSetGameHistory = this.props.setGameHistory;
        getGameHistory(datesHistory[historyCounter]);
        //recursion until pastDays
        function getGameHistory(d){  
            getGames(d).then(
                (response) => { 
                    tempGameHistory = [...tempGameHistory, ...response];
                    historyCounter++;
                    if(historyCounter < numDays){
                        getGameHistory(datesHistory[historyCounter]);
                    }
                    else{
                        if(tempGameHistory.length === 0){
                            tempGameHistory = [0]
                        }
                        console.log(tempGameHistory);
                        tempSetGameHistory(tempGameHistory);                     
                    }
                },
                (error) => {
                    console.log(error);
            });  
        }        
        this.props.setDate("20190313");                  
    }
    componentWillReceiveProps(nextProps){
        //change list of games when date is changed only
        if(nextProps.date !== this.props.date){
            this.props.setDateGames([]);
            getGames(nextProps.date).then(
                (response) => { 
                    console.log(response);
                    this.props.setDateGames(response.length===0?[0]:response);
                },
                (error) => {
                  console.log(error);
              });  
        }
    }

    updateDate(date){
        //set redux date state
        this.props.setDate(date);
    }

    showBoxScore(game){
        let that = this;
        that.props.setShowPlayByPlay(false);
        that.props.setPlayByPlay(null);
        that.props.setGameBoxScore(game);
        //get box score data and scroll to top
        getBoxScore(game.startDateEastern, game.gameId).then(
            (response) => { 
                console.log(response);
                that.props.setBoxScore(response);
                window.scrollTo({
                    top: 0,
                    behavior: 'smooth',
                  });
            },
            (error) => {
              console.log(error);
          });  
    }

    render(){
        return(
            <div className="parent">
                <div className="header-container">
                    <div className="parent-container">
                        <header className="header">
                            <div className="logo">
                                <img alt='NBA push' src={images['logo']}/> <span>push</span>
                            </div>
                        </header>     
                    </div>
                </div>
                <div className="parent-container">
                    { 
                        this.props.boxScore !== undefined && this.props.boxScore !== 0 && this.props.gameBoxScore !== null?
                            <section>
                                <BoxScoreComponent showPlayByPlay={this.props.showPlayByPlay} 
                                setShowPlayByPlay={this.props.setShowPlayByPlay}
                                playByPlay={this.props.playByPlay} 
                                setPlayByPlay={this.props.setPlayByPlay}
                                gameBoxScore={this.props.gameBoxScore} 
                                boxScore={this.props.boxScore}
                                setBoxScore={this.props.setBoxScore}/>
                            </section>
                        : ''
                    }
                    <section>
                        <div className="game-content">
                            <div className="date-picker">
                                <div className="selected-title">
                                    <h1>
                                    {
                                        this.props.date===''?
                                            'Select date to show scheduled games' 
                                        : 
                                            this.props.dateGames.length > 0 && this.props.dateGames[0] === 0?
                                                'Selected date has no games'
                                            :
                                                moment(this.props.date).format('dddd, MMMM Do YYYY')
                                    }
                                    </h1>
                                </div>
                                <div className="selected-date-container"><span>Select</span><DateComponent setDate={this.props.setDate} datePicker={this.props.datePicker} setDatePicker={this.props.setDatePicker} updateDate={this.updateDate} /></div>
                            </div>
                            {
                                this.props.date===''?'' 
                                : 
                                this.props.dateGames.length > 0? 
                                    this.props.dateGames[0] === 0?
                                        ''
                                    :
                                        <GameListComponent showBoxScore={this.showBoxScore} gameList={this.props.dateGames} title={null}/>
                                :
                                    <div className="loading-container">
                                        <LoadingComponent type={'spin'} color={'#8d8f9b'}/>
                                    </div>
                                
                            }

                        </div>
                    </section>                
                    <section>
                        <div className="game-content">
                            {
                                this.props.gameHistory.length > 0? 
                                    this.props.gameHistory[0] === 0?
                                        <h1 className="game-nofound">No previous games found</h1>
                                    :
                                        <GameListComponent showBoxScore={this.showBoxScore} gameList={this.props.gameHistory} title={'Previous Games'}/>
                                :
                                    <div className="loading-container">
                                        <LoadingComponent type={'spin'} color={'#8d8f9b'}/>
                                    </div>
                                
                            }
                        </div>
                    </section>
                </div>
                <div className="footer">
                    Copyright  2019 NBA push. All rights reserved.
                </div>
            </div>
        );
    };
};

export default HomeComponent;